/* 分页样式 */

// 首页

#index_slides {
    .swiper_slide_wrapper {
        background-size: cover;
        background-position: center;
        height: 679px;
        @media (max-width: 767px) {
            height: 200px;
            background-position: 70%;
        }
        @media (min-width: 768px) and (max-width: 991px) {
            height: 350px;
        }
        @media (min-width: 992px) and (max-width: 1199px) {
            height: 450px;
        }
        @media (min-width: 1200px) {
            // background-position: 60%;
        }
    }
    .swiper-pagination {
        @media (min-width: 1200px) {
            bottom: 30px;
        }
    }
    .swiper-pagination-bullet {
        background-color: #cbc2ba;
        opacity: 1;
        width: 16px;
        height: 16px;
        margin: 0 10px;
        @media (max-width: 1199px) {
            width: 10px;
            height: 10px;
        }
    }
    .swiper-pagination-bullet-active {
        background-color: #752a4d;
        // width: 18px;
        // height: 18px;
    }
    .swiper-button-prev, .swiper-button-next {
        display: none;
    }
}

#index_main {
    background-color: #fff;
    padding-top: 15px;
    .container-fluid {
        @media (min-width: 768px) {
            padding-left: 10px;
            padding-right: 10px;
            > .row {
                margin-left: -5px;
                margin-right: -5px;
                > .col-lg-3 {
                    padding-left: 5px;
                    padding-right: 5px;
                }
            }
        }
        @media (max-width: 1199px) {
            > .row {
                > .col-lg-3 {
                    margin-bottom: 15px;
                }
            }
        }
    }
}
.index_main_newsletter {
    background-color: #ececec;
    margin-left: auto;
    margin-right: auto;
    @media (min-width: 1200px) {
        height: 207px;
        padding: 40px 30px;
    }
    @media (min-width: 768px) and (max-width: 991px) {
        height: 165px;
        padding-left: 20px;
        padding-right: 20px;
        padding-top: 10px;
    }
    @media (min-width: 992px) and (max-width: 1199px) {
        height: 208px;
        padding: 40px 30px;
    }
    @media (max-width: 767px) {
        padding: 15px;
    }
}
.index_main_newsletter_title {
    font-size: 24px;
    color: #333;
}
.index_main_newsletter_desc {
    font-size: 14px;
    color: #666;
    margin-top: 5px;
    margin-bottom: 25px;
}
.index_main_newsletter_form {
    background-color: #c8c8c8;
    border-radius: 2px;
    .form-group {
        padding-top: 3px;
        padding-bottom: 3px;
        margin-left: 0;
        margin-right: 0;
        > div[class^="col-"] {
            padding-left: 0;
            padding-right: 0;
            text-align: center;
        }
        @media (min-width: 992px) {
            .col-xs-10 {
                width: 86%;
            }
            .col-xs-2 {
                width: 14%;
            }
        }
        input.form-control {
            background-color: transparent;
            border: none;
            border-radius: 0;
            outline: none;
            box-shadow: none;
            &::placeholder {
                color: #666;
            }
        }
        .btn.btn-default {
            border: none;
            background-color: transparent;
            padding: 6px 10px;
            i.fa {
                font-size: 16px;
            }
            i.fa-user-plus {
                display: none;
            }
            &:hover, &.input_focus {
                i.fa-envelope-o {
                    display: none;
                }
                i.fa-user-plus {
                    display: inline-block;
                }

            }
        }
    }
}

#index_btm_icons {
    background-color: #fff;
    padding: 40px 0;
    @media (max-width: 1199px) {
        padding: 30px 0;
    }
    .row {
        margin-left: 0;
        margin-right: 0;
        .col-md-1 {
            padding-left: 0;
            padding-right: 0;
            @media (max-width: 991px) {
                margin-bottom: 10px;
            }
        }
    }
}

// 关于我们页面

#about_banner {
    .page_banner {
        @media (max-width: 767px) {
            background-position: 75%;
        }
    }
}
#about_page_title {
    font-size: 24px;
    color: #333333;
    text-align: center;
    padding-top: 70px;
    @media (max-width: 991px) {
        padding-top: 40px;
    }
}

#about_icons {
    padding-top: 70px;
    padding-bottom: 70px;
    @media (max-width: 991px) {
        padding-top: 40px;
        padding-bottom: 40px;
    }
    .row {
        .col-md-3 {
            @media (max-width: 767px) {
                margin-bottom: 20px;
            }
        }
    }
}
.about_icons_item_txt {
    font-size: 14px;
    color: #333333;
    text-align: center;
    padding-top: 20px;
    @media (max-width: 767px) {
        height: 60px;
        overflow: hidden;
    }
}

#about_content {
    p {
        font-size: 14px;
        line-height: 2.2em;
        color: #333333;
        text-align: justify;
        margin-bottom: 40px;
        @media (max-width: 767px) {
            line-height: 2em;
            text-align: left;
            margin-bottom: 20px;
        }
        @media (min-width: 768px) and (max-width: 991px) {
            margin-bottom: 30px;
        }
        &:first-child {
            float: left;
            margin-right: 25px;
            margin-bottom: 25px;
            @media (max-width: 767px) {
                float: none;
                margin-right: 0;
                margin-bottom: 20px;
            }
            @media (min-width: 768px) and (max-width: 991px) {
                img {
                    width: 300px;
                }
            }
            @media (min-width: 992px) and (max-width: 1199px) {
                img {
                    width: 400px;
                }
            }
        }
        img {
            display: block;
            max-width: 100%;
            margin-left: auto;
            margin-right: auto;
        }
    }
}

#about_gallery {
    padding-bottom: 60px;
    @media (max-width: 991px) {
        padding-bottom: 30px;
    }
    .row {
        margin-left: -5px;
        margin-right: -5px;
        .col-md-4 {
            padding-left: 5px;
            padding-right: 5px;
            margin-bottom: 10px;
        }
    }
}

// 设计师页面

#designer_banner {
    .page_banner {
        @media (max-width: 767px) {
            background-position: 35%;
        }
    }
}

#designer {
    padding-top: 70px;
    padding-bottom: 70px;
    @media (max-width: 991px) {
        padding-top: 40px;
        padding-bottom: 40px;
    }
    > .row {
        margin-left: 0;
        margin-right: 0;
        > div[class^="col-"] {
            padding-left: 0;
            padding-right: 0;
        }
        > .col-md-4 {
            @media (max-width: 991px) {
                margin-bottom: 30px;
            }
        }
    }
}
.designer_info {
    padding-left: 30px;
    padding-right: 60px;
    @media (min-width: 768px) and (max-width: 991px) {
        padding-left: 30px;
        padding-right: 30px;
    }
    @media (max-width: 767px) {
        padding-left: 0;
        padding-right: 0;
    }
}
.designer_name {
    font-size: 30px;
    color: #231b12;
}
.designer_desc_content, .designer_desc_title {
    font-size: 14px;
    color: #231b12;
}
.designer_desc_title {
    padding-top: 20px;
    padding-bottom: 20px;
}
.designer_desc_content {
    padding-bottom: 50px;
    @media (max-width: 991px) {
        padding-bottom: 30px;
    }
    p {
        line-height: 2em;
    }
}

// 新闻列表页

#news_banner {
    .page_banner {
        @media (max-width: 767px) {
            background-position: 80%;
        }
        @media (min-width: 1200px) {
            background-position: 70%;
        }
    }
}

#news {
    padding-top: 80px;
    @media (max-width: 991px) {
        padding-top: 40px;
    }
}
.news_item {
    transition: background-color 0.3s ease;
    > .row {
        > .col-md-3 {
            @media (max-width: 767px) {
                margin-bottom: 20px;
            }
        }
    }
    &:hover {
        @media (min-width: 1200px) {
            background-color: #fff;
        }
    }
    &:first-child {
        background-color: #fff;
        > .row {
            > .col-md-3 {
                @media (min-width: 768px) {
                    width: 50%;
                }
            }
            > .col-md-9 {
                @media (min-width: 768px) {
                    width: 50%;
                }
                .news_item_info {
                    padding-top: 50px;
                    @media (min-width: 768px) and (max-width: 991px) {
                        padding-top: 30px;
                    }
                    @media (max-width: 767px) {
                        padding-top: 0;
                        padding-left: 15px;
                        padding-right: 15px;
                        padding-bottom: 15px;
                    }
                    .news_item_desc {
                        @media (min-width: 768px) {
                            max-height: 75px;
                            overflow: hidden;
                        }
                    }
                }
            }
        }
    }
}
.news_item + .news_item {
    margin-top: 50px;
    @media (max-width: 991px) {
        margin-top: 30px;
    }
}
.news_item_info {
    padding-left: 10px;
    padding-right: 40px;
    @media (max-width: 767px) {
        padding-left: 0;
        padding-right: 0;
    }
    @media (min-width: 768px) and (max-width: 991px) {
        padding-left: 0;
        padding-right: 0;
    }
    @media (min-width: 1200px) {
        padding-top: 15px;
    }
}
.news_item_title {
    a {
        font-size: 18px;
        color: #333333;
        display: block;
        max-height: 52px;
        overflow: hidden;
    }
}
.news_item_time {
    font-size: 14px;
    color: #333333;
    padding-top: 20px;
    padding-bottom: 20px;
}
.news_item_desc {
    font-size: 14px;
    line-height: 1.8em;
    color: #888888;
    @media (min-width: 768px) {
        max-height: 50px;
        overflow: hidden;
    }
}
.news_pagination {
    text-align: center;
    padding-top: 30px;
    padding-bottom: 30px;
    @media (max-width: 991px) {
        padding-top: 10px;
        padding-bottom: 10px;
    }
}
